import Trash from '../../apis/trash'
import { Message } from 'element-ui'
const state= {
  curTrashNoteId :null,  //id
  trashNotes : null // []
  }
const getters = {  //响应式
    trashNotes: state=>  state.trashNotes  || [],
  curTrashNote: (state,getters) => {
      if(!state.curTrashNoteId){
        return getters.trashNotes[0] || {}
      }else {
        return state.trashNotes.find(note=>note.id == state.curTrashNoteId) || {}
      }
  },
  belongTo: (state, getters, rootState, rootGetters) => {
    let notebook = rootGetters.notebooks.find(notebook => notebook.id == getters.curTrashNote.notebookId) || {}
    return notebook.title || ''
  }
}
const mutations = {
  setTrashNotes(state,payload){
   state.trashNotes = payload.trashNotes  // {trashNotes: [?????], note:{??} , curTrashNoteId }
 },
  addTrashNote(state,payload){
    state.trashNotes.unshift(payload.note)
  },
  deleteTrashNote(state,payload){
    state.trashNotes = state.trashNotes.filter(note=>note.id != payload.noteId)
  },
  setCurTrashNote(state,payload = {}){
    state.curTrashNoteId = payload.curTrashNoteId  //让id变curTrashNote自动变
  }
}
const actions = {
 getTrashNotes({commit}){
   return Trash.getAll().then(response=>{
     commit('setTrashNotes',{trashNotes:response.data})
   })
 },
  deleteTrashNote({commit},payload) {     //{noteId: ???/]
    return Trash.deleteNote({noteId : payload.noteId}).then(response=>{
      commit('deleteTrashNote', {noteId: payload.noteId})
      Message.success(response.msg)
    })
 },
 revertTrashNote({commit},payload) {      //{noteId: ???/]
    return Trash.revertNote({noteId : payload.noteId}).then(response=>{
      commit('deleteTrashNote', {noteId: payload.noteId})
      Message.success(response.msg)
    })
  },
}
export default  {
  state,
  mutations,
  getters,
  actions
}



