import Auth from '../../apis/auth'
import request from "../../helpers/request";
import Router from '../../router/index'
const state = {
  user: null
}
const getters = {
  username: state => state.user === null ? '未登录' : state.user.username,
  slug: state => state.user === null ? '未' : state.user.username.charAt(0)
}
const mutations = {
  setUser(state, payload) {
    state.user = payload.user   //  payload是 {user : {username: ????}}
  }
}
const actions = {
  login({commit}, {username, password}) {  //  payload是 {username:???,password:????}
    return Auth.login({username, password})
      .then(response => {
        commit('setUser', {user: response.data})
      })
  },
  register({commit}, {username, password}) {  //  payload是 {username:???,password:????}
    return Auth.register({username, password})
      .then(response => {
        commit('setUser', {user: response.data})
      })
  },
  checkLogin({commit},payload){
   return  Auth.getInfo().then(response => {
     if (!response.isLogin) {
       Router.push(payload).catch(err => {})
     }else {
       commit('setUser',{user:response.data})
     }
   })
  },
  logout({commit},payload){
    return Auth.logout().then(response=>{
      commit('setUser',{user:null})
      Router.push(payload).catch(err => {})
    })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



