import request from '../helpers/request'
import { friendlyDate } from "../helpers/util";

const URL = {
  GET:'/notes/trash',
  REVERT:'/notes/:noteId/revert',
  DELETE:'/notes/:noteId/confirm'
}
export default  {
  getAll(){
    return new Promise((resolve,reject)=>{  //在return new Promise 比较createdAt排序
      request(URL.GET, 'GET').then((response)=>{
        response.data = response.data.sort((notebook1, notebook2) => notebook1.createAt < notebook2.createAt ? 1 : -1)
        response.data.forEach(notebook=>{
          notebook.createdAtFriendly = friendlyDate(notebook.createdAt)
          notebook.updatedAtFriendly = friendlyDate(notebook.updatedAt)
        })
        resolve(response)
      }).catch(err=>{
        reject(err)
      })
    })
  },
  revertNote({noteId}){
    return  new Promise ((resolve,reject)=>{
      request(URL.REVERT.replace(':noteId',noteId),'PATCH').then(response=>{
        resolve(response)
      }).catch(err=>{
        reject(err)
      })
    })
  },
  deleteNote({noteId}){
    return new Promise((resolve,reject)=>{
      request(URL.DELETE.replace(':noteId',noteId),'DELETE').then((response)=>{
        resolve(response)
      }).catch((err)=>{
        reject(err)
      })
    })


  }
}



