<template>
  <div id="sidebar">
    <Avatar></Avatar>
    <div class="icons">
      <router-link to="/note" title="笔记">
        <Icon name="note" class="iconfont icon-note"></Icon>
      </router-link>
      <router-link to="/notebooks" title="笔记本">
        <Icon name="notebook" class="iconfont icon-notebook"></Icon>
      </router-link>
      <router-link to="/trash" title="回收站">
        <Icon name="trash" class="iconfont icon-trash"></Icon>
      </router-link>
    </div>
    <div class="logout" @click="onLogout">
      <Icon name="logout" class="iconfont icon-logout" ></Icon>
    </div>
  </div>
</template>

<script>
import Icon from './Icon.vue'
import Avatar from './Avatar.vue'
import Auth from '../apis/auth'
import eventBus from "../helpers/eventBus";
import {mapActions} from "vuex";

export default {
  components: {Icon, Avatar},
  methods:{
    ...mapActions(['logout']),
    onLogout(){
      this.$confirm('确定注销登录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'info'
      }).then(()=>{
        this.logout({path:'/login'})
      })
    }
  }

}
</script>
<style lang="less" scoped>
#sidebar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  //position: relative;
  max-width: 70px;
  //text-align: center;
  background-color: #2c333c;

  .icons {
    margin-top: 15px;

    a {
      width: 100%;
      transition: all 0.3s;
      padding: 12px 20px;
      display: block;
    }

    a:hover {
      background-color: #8a8a8c;
    }

    .router-link-active {
      background-color: #5e6266;
    }
  }

  .logout {
    position: absolute;
    bottom: 20px;
    width: 100%;
    text-align: center;
    cursor: pointer;
  }

  .iconfont {
    color: #fff;
    width: 2em;
    height: 2em;
  }
}
</style>



